html,
body {
  font-size: 62.5%;
}

body {
  line-height: 1.5;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  background-color: #000;
  color: white;
}

address {
  font-style: normal;
}

img {
  height: auto;
  max-width: 100%;
}

label {
  cursor: pointer;
  user-select: none;
}

textarea {
  resize: vertical;
}

::-webkit-input-placeholder {
  text-overflow: ellipsis;
}

:-moz-placeholder {
  text-overflow: ellipsis;
}

::-moz-placeholder {
  text-overflow: ellipsis;
}

:-ms-input-placeholder {
  text-overflow: ellipsis;
}

input[placeholder] {
  text-overflow: ellipsis;
}

input::-moz-placeholder {
  text-overflow: ellipsis;
}

input:-moz-placeholder {
  text-overflow: ellipsis;
}

input:-ms-input-placeholder {
  text-overflow: ellipsis;
}

:focus::-webkit-input-placeholder {
  color: transparent;
}

:focus::-moz-placeholder {
  color: transparent;
}

:focus:-moz-placeholder {
  color: transparent;
}

:focus:-ms-input-placeholder {
  color: transparent;
}

.video {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}

.video iframe,
.video object,
.video embed {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.case-item {
  position: relative;
  padding: 38px;
  padding-bottom: 65px;
  text-decoration: none;
  color: white;
  min-height: 350px;
}

@media (max-width: 768px) {
  .case-item {
    padding: 33px;
  }
}

.case-item:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  transition: width 0.3s;
  background-color: #0d71fc;
  z-index: 0;
}

.case-item:hover:after {
  background-color: #0d71fc;
  width: 100%;
}

.case-item:hover .case-item__link {
  color: white;
}

.case-item--fav {
  background-color: #0d71fc;
}

.case-item__image-cont {
  position: relative;
  display: flex;
  align-items: flex-end;
  min-height: 85px;
  margin-bottom: 38px;
  z-index: 1;
}

.case-item__image-cont img {
  max-width: 100%;
}

.case-item__title {
  position: relative;
  font-size: 16px;
  line-height: 24px;
  font-weight: 800;
  width: 80%;
  min-height: 124px;
  z-index: 1;
}

.case-item__link-cont {
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  bottom: 38px;
  left: 0;
  padding-left: inherit;
  padding-right: inherit;
  z-index: 1;
}

.case-item__link {
  transition: color 0.3s;
  position: relative;
  display: inline-block;
  font-size: 12px;
  line-height: 17px;
  vertical-align: bottom;
  font-weight: 300;
  color: #0d71fc;
  z-index: 1;
}

.page-staff {
  background-color: #000;
  color: #fff;
}

.page-staff__cases {
  display: flex;
  justify-content: left;
  flex-grow: 1;
  flex-wrap: wrap;
}

.page-staff__case-item {
  width: 340px;
}

@media (max-width: 479px) {
  .page-staff__case-item {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .page-staff__case-item {
    width: 50%;
  }
}

@media (max-width: 1024px) {
  .page-staff__case-item {
    width: 33.33%;
  }
}

@media (max-width: 1280px) {
  .page-staff__case-item {
    width: 25%;
  }
}

@media (max-width: 1440px) {
  .page-staff__case-item {
    width: 20%;
  }
}

@media (max-width: 1660px) {
  .page-staff__case-item {
    width: 16.667%;
  }
}

@media (min-width: 1661px) {
  .page-staff__case-item {
    width: 14.2857%;
  }
}

